"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";
import {
    LayoutDashboard,
    ShoppingCart,
    Package,
    Users,
    Settings,
    ChevronLeft,
    Menu,
    Landmark
} from "lucide-react";
import { Button } from "@/components/ui/button";

interface AdminSidebarProps {
    isOpen: boolean;
    setIsOpen: (open: boolean) => void;
}

export function AdminSidebar({ isOpen, setIsOpen }: AdminSidebarProps) {
    const pathname = usePathname();

    const navItems = [
        { name: "Dashboard", href: "/admin", icon: LayoutDashboard },
        { name: "Orders", href: "/admin/orders", icon: ShoppingCart },
        { name: "Financials", href: "/admin/transactions", icon: Landmark },
        { name: "Products", href: "/admin/products", icon: Package },
        { name: "Users", href: "/admin/users", icon: Users },
        { name: "Settings", href: "/admin/settings", icon: Settings },
    ];

    return (
        <>
            {/* Mobile Overlay */}
            <div
                className={cn(
                    "fixed inset-0 bg-black/50 z-40 lg:hidden transition-opacity",
                    isOpen ? "opacity-100" : "opacity-0 pointer-events-none"
                )}
                onClick={() => setIsOpen(false)}
            />

            {/* Sidebar */}
            <aside
                className={cn(
                    "fixed top-0 left-0 z-50 h-screen transition-transform duration-300 bg-white dark:bg-slate-950 border-r border-slate-200 dark:border-slate-800 w-64 shadow-lg",
                    isOpen ? "translate-x-0" : "-translate-x-full lg:translate-x-0"
                )}
            >
                <div className="flex flex-col h-full">
                    {/* Header */}
                    <div className="h-16 flex items-center justify-between px-6 border-b border-transparent">
                        <Link href="/admin" className="flex items-center gap-2 font-bold text-2xl text-slate-800 dark:text-slate-100">
                            <span className="text-primary text-3xl">‚ùñ</span>
                            <span>Sneat</span>
                        </Link>
                        <Button
                            variant="ghost"
                            size="icon"
                            className="lg:hidden"
                            onClick={() => setIsOpen(false)}
                        >
                            <ChevronLeft className="h-5 w-5" />
                        </Button>
                    </div>

                    {/* Nav Items */}
                    <nav className="flex-1 px-4 py-4 space-y-1 overflow-y-auto">
                        {navItems.map((item) => {
                            const isActive = pathname === item.href;
                            return (
                                <Link
                                    key={item.href}
                                    href={item.href}
                                    onClick={() => setIsOpen(false)}
                                    className={cn(
                                        "flex items-center gap-3 px-4 py-3 rounded-lg text-sm font-medium transition-all duration-200",
                                        isActive
                                            ? "bg-indigo-50 text-primary shadow-sm dark:bg-slate-900 dark:text-indigo-400"
                                            : "text-slate-600 hover:bg-slate-50 hover:text-slate-900 dark:text-slate-400 dark:hover:bg-slate-900 dark:hover:text-slate-200"
                                    )}
                                >
                                    <item.icon className={cn("w-5 h-5", isActive ? "text-primary dark:text-indigo-400" : "text-slate-400")} />
                                    {item.name}
                                </Link>
                            );
                        })}
                    </nav>

                    {/* Footer (optional) */}
                    <div className="p-4 border-t border-slate-100 dark:border-slate-900">
                        <div className="bg-slate-50 dark:bg-slate-900 rounded-lg p-4 text-center">
                            <p className="text-xs text-slate-500 mb-2">Need Help?</p>
                            <Button size="sm" variant="outline" className="w-full text-xs">Docs</Button>
                        </div>
                    </div>
                </div>
            </aside>
        </>
    );
}
